.sub-header {
	padding-bottom: 10px;
	border-bottom: 1px solid #eee;
}


.navbar-fixed-top {
	border: 0;
}

.sidebar {
	display: none;

	@media (min-width: 768px) {
		position: fixed;
		top: 51px;
		bottom: 0;
		left: 0;
		z-index: 1000;
		display: block;
		padding: 20px;
		overflow-x: hidden;
		overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
		background-color: #f5f5f5;
		border-right: 1px solid #eee;
	}
}

/* Sidebar navigation */
.nav-sidebar {
	margin-right: -21px; /* 20px padding + 1px border */
	margin-bottom: 20px;
	margin-left: -20px;

	> li > a {
		padding-right: 20px;
		padding-left: 20px;
	}
	> .active > a,> .active > a:hover,> .active > a:focus{
		color: #fff;
		background-color: #428bca;
	}
}
/*
 * Main content
 */
.main {
	padding: 20px;

	.page-header {
		margin-top: 0;
	}

	@media (min-width: 768px) {
		padding-right: 40px;
		padding-left: 40px;
	}
}
/*
 * Placeholder dashboard ideas
 */
.placeholders {
	margin-bottom: 30px;
	text-align: center;

	h4 {
		margin-bottom: 0;
	}
}
.placeholder {
	margin-bottom: 20px;
	img {
		display: inline-block;
		border-radius: 50%;
	}
}

// categories
.category-panel{
	.thumbnail{
		height:140px;
		background-size:100%;
	}
}
.image.thumbnail{
	margin-right: 3.33%;
	height: 140px;
	background-size: 100% !important;
	background-position: 50%;
	background-repeat: no-repeat;
	input[type="file"]{
		margin: 0;
		padding: 0;
		border: none;
		width: 0;
		height: 0;
		overflow: hidden;
		float: left;
		opacity: 0;
	}
}